import * as React from 'react';
import { Link, graphql } from 'gatsby';
import { GatsbyImage } from 'gatsby-plugin-image';

import Bio from '../components/bio';
import Layout from '../components/layout';
import Seo from '../components/seo';

const BlogPage = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`;
  const social = data.site.siteMetadata?.social;
  const posts = data.allMarkdownRemark.nodes;

  if (posts.length === 0) {
    return (
      <Layout location={location} title={siteTitle}>
        <Seo title="All Posts" />
        <Bio />
        <p>
          No blog posts found. Add markdown posts to "content/blog" (or the directory you specified for the
          "gatsby-source-filesystem" plugin in gatsby-config.js).
        </p>
      </Layout>
    );
  }

  return (
    <Layout location={location} title={siteTitle}>
      <Seo title="All Posts" />

      <h1 className="text-3xl lg:text-5xl md:text-5xl mb-4 font-bold tracking-tighter animated-text-bg bg-gradient-oceanic">
        Blog
      </h1>
      <p className="mb-8">
        If you want to keep track of what I'm writing about â€” follow me on{` `}
        <a
          href={`https://twitter.com/${social?.twitter || ``}`}
          title="Twitter"
          target="_blank"
          className="text-blue-300 hover:text-blue-400 fill-blue-300 hover:fill-blue-400 transition-colors inline-block"
        >
          <span className="flex">
            <span>Twitter</span>
            <svg
              className="w-4 h-4 fill-inherit self-center ml-1"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
            >
              <path d="M23.643 4.937c-.835.37-1.732.62-2.675.733.962-.576 1.7-1.49 2.048-2.578-.9.534-1.897.922-2.958 1.13-.85-.904-2.06-1.47-3.4-1.47-2.572 0-4.658 2.086-4.658 4.66 0 .364.042.718.12 1.06-3.873-.195-7.304-2.05-9.602-4.868-.4.69-.63 1.49-.63 2.342 0 1.616.823 3.043 2.072 3.878-.764-.025-1.482-.234-2.11-.583v.06c0 2.257 1.605 4.14 3.737 4.568-.392.106-.803.162-1.227.162-.3 0-.593-.028-.877-.082.593 1.85 2.313 3.198 4.352 3.234-1.595 1.25-3.604 1.995-5.786 1.995-.376 0-.747-.022-1.112-.065 2.062 1.323 4.51 2.093 7.14 2.093 8.57 0 13.255-7.098 13.255-13.254 0-.2-.005-.402-.014-.602.91-.658 1.7-1.477 2.323-2.41z"></path>
            </svg>
          </span>
        </a>
      </p>

      <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 place-content-stretch">
        {posts.map((post) => {
          const title = post.frontmatter.title || post.fields.slug;
          const Image = post.frontmatter.featuredImage
            ? post.frontmatter.featuredImage.childImageSharp.gatsbyImageData
            : '';

          return (
            <Link key={post.fields.slug} to={post.fields.slug} itemProp="url">
              <div className="rounded-lg bg-white dark:bg-neutral-900 shadow-md hover:shadow-lg dark:shadow-neutral-700/50 hover:dark:shadow-orange-500/50 h-full">
                {Image ? (
                  <GatsbyImage
                    image={Image}
                    alt={post.frontmatter.title + ' - Featured image'}
                    className="rounded-t-lg w-full"
                  />
                ) : (
                  ''
                )}
                <div className="post-list-item p-4" itemScope itemType="http://schema.org/Article">
                  <h2 className="m-0 mb-1 text-xl font-bold">{title}</h2>

                  <p
                    className="text-sm text-slate-600 dark:text-slate-200"
                    dangerouslySetInnerHTML={{
                      __html: post.frontmatter.description || post.excerpt,
                    }}
                  />

                  <small>
                    {post.frontmatter.date} &bull; {post.fields.timeToRead.text}
                  </small>
                </div>
              </div>
            </Link>
          );
        })}
      </div>
    </Layout>
  );
};

export default BlogPage;

export const pageQuery = graphql`
  query BlogPage {
    site {
      siteMetadata {
        title
        social {
          twitter
        }
      }
    }
    allMarkdownRemark(filter: { frontmatter: { draft: { ne: true } } }, sort: { frontmatter: { date: DESC } }) {
      nodes {
        excerpt
        fields {
          slug
          timeToRead {
            text
          }
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          featuredImage {
            childImageSharp {
              gatsbyImageData(width: 360, placeholder: BLURRED, formats: [AUTO, WEBP, AVIF], quality: 90)
            }
          }
        }
      }
    }
  }
`;
